/* STATIC STYLES (desktop-first) */

#portfolio {
  background: linear-gradient(45deg, var(--color-primary-darker), var(--color-primary-dark), var(--color-primary-darker));

  margin-block: 6rem;
}

.portfolio-container {
  display: flex;
  flex-direction: column;
}

.portfolio-content {
  display: flex;
  flex-direction: column;

  gap: 3rem;
}

.portfolio-img-container {
  margin: 0 3rem;
}

.portfolio-header-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-bottom: 2rem;

  gap: 2rem;
}

.portfolio-img {
  height: 25rem;

  object-fit: contain;
}

.html-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  gap: 2rem;
}

.link {
  margin-left: 15px;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  #portfolio {
    margin-block: 6rem;
  }
}

@media (max-width: 992px) {
  #portfolio {
    margin-block: 5rem;
  }

  .portfolio-content {
    gap: 2rem;
  }

  .portfolio-img-container {
    margin: 0 1rem;
  }

  .portfolio-header-content {
    margin-bottom: 0;

    gap: 0;
  }
}

@media (max-width: 768px) {
  #portfolio {
    margin-block: 4rem;
  }

  .portfolio-content h4 {
    margin-top: 1rem;
  }

  .portfolio-img-container {
    margin: -3rem 1rem;
  }
}

@media (max-width: 576px) {
  #portfolio {
    margin-block: 3rem;
  }

  .portfolio-content {
    gap: 1.5rem;
  }
}
