/* STATIC STYLES (desktop-first) */

.desktop {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 5rem;

  background: var(--color-primary-darkest);

  text-align: center;
  color: var(--color-secondary);

  margin-inline: 2rem;
}

.desktop a {
  margin-left: 2rem;

  color: var(--color-primary);
}

.desktop a:hover {
  color: var(--color-secondary);
}

.mobile {
  position: fixed;
  bottom: 5rem;
  left: 50%;
  z-index: 100;

  display: none;

  border-radius: 3rem;

  background: var(--color-blur);
  backdrop-filter: blur(1.5rem);

  transform: translateX(-50%);
}

.mobile > a {
  display: flex;

  padding: 0.8rem 1.1rem;

  border-radius: 50%;

  background: transparent;

  color: var(--color-primary-text);
}

.mobile > a:hover {
  color: var(--color-secondary);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .desktop {
    display: none;
  }

  .mobile {
    bottom: 5rem;

    display: flex;
  }
}

@media (max-width: 576px) {
  .mobile {
    bottom: 3rem;
  }
}
