/* STATIC STYLES (desktop-first) */

.experience-content {
  display: grid;
  grid-template-areas: 'front back others';
  grid-auto-columns: 1fr;

  width: 100%;

  gap: 3rem;
}

.experience-content-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  gap: 1rem;
}

.experience-content-card > p {
  display: flex;
  justify-content: center;
  align-items: center;

  white-space: nowrap;
}

.experience-content-card > p > .color-secondary:first-child {
  margin-right: 1rem;
}

.card2:nth-child(1) {
  grid-area: front;
}
.card2:nth-child(2) {
  grid-area: back;
}
.card2:nth-child(3) {
  grid-area: others;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .experience-content {
    grid-template-areas: 'front back'
    'front others';
  }
}

@media (max-width: 768px) {
}

@media (max-width: 576px) {
  .experience-content {
    grid-template-areas: 'front'
    'back'
    'others';
  }
}
