/* STATIC STYLES (desktop-first) */

.card3 {
  max-width: 85vw;

  border-radius: 1rem;

  background: var(--color-primary-darkest);
  box-shadow: rgba(0, 0, 0, 0.25) 0 25px 50px -12px;

  transform: perspective(750px) translate3d(0px, -10px, -100px) rotateX(25deg) scale(0.95, 1);
  transition: transform 400ms ease;
}

.card3:hover {
  transform: translate3d(0px, 0px, -200px);
}

.card3-content {
  display: flex;
}

.card3-content > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.highlight:hover {
  border: 1px solid var(--color-secondary);

  background: var(--color-primary-darker);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .card3 {
    transform: translate3d(0px, 0px, -200px);
  }

  .card3-content {
    flex-direction: column !important;
    justify-content: center;
    align-items: center;

    gap: 1rem !important;
  }

  .card3-content > div {
    width: 100% !important;
  }
}

