/* STATIC STYLES (desktop-first) */

.socials {
  position: absolute;
  top: 20vh;
  right: 6rem;

  display: flex;
  flex-direction: column;
  align-items: center;

  gap: 1rem;
}

.socials a {
  color: var(--color-primary-darkest);
}

.socials a:hover {
  color: var(--color-secondary);
}

