/* STATIC STYLES (desktop-first) */

.services-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;

  gap: 3rem;
}

.services-content > * {
  flex: 0 0 20rem;
}

.services-content-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;

  padding: 1rem 2rem;

  gap: 1rem;
}

.services-content-card > p {
  display: flex;
  justify-content: center;
  align-items: center;

  text-align: start;
}

.services-content-card > p > .color-secondary:first-child {
  margin-right: 1rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .services-content {
    flex-direction: row;

    gap: 3rem;
  }

  .services-content-card {
    padding: 0 1rem;
  }
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .services-content {
    flex-direction: column;

    gap: 2rem;
  }
}

@media (max-width: 576px) {
  .services-content {
    gap: 1.5rem;
  }
}
